    padding: 0;
    margin: 0;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
:root {
    --bgColor: rgb(237, 233, 247);
    --shadow: 3px 3px 3px rgb(0, 0, 0, 0.3);
    --headerColour : rgb(13, 20, 42);
    --colorOnCheck : rgb(141, 179, 249);
    --colorBeforeCheck : rgb(96, 106, 170);
}

body {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    background:url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
    background-repeat: no-repeat;
}

.header {
    width: 100%;
    height: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    background-color: var(--headerColour);
}

.loader {
    width: 50px;
    height: 50px;
    background : url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-size:contain;
    background-repeat: no-repeat;
}

form{
    display:flex;
    width: 22.8%;
}

input {
    width: 100%;
    padding: 10px 20px;
    border: none;
    border-radius: 20px;
    outline: none;
    box-shadow: var(--shadow);
    font-weight: 500;
}

.weather-card {
    display: grid;
    grid-template-columns: 1fr .4fr;
    grid-template-rows: 25px 1fr;
    box-shadow: var(--shadow);
    border-radius: 10px;
    background-color:white;
    margin: 0px 10px;
}

.weather-card > *:not(:first-child) {
    padding: 1em 5%;
}

.weather-card > *:not(.temp, .weather-icon){
    grid-column: 1/-1;
}

.weather-icon {
    align-self: center;
    grid-column: 2/-1;
    grid-row : 2/3;
    width: 85px;
    height: 85px;
}

.address{
    background-color: #333;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    color: #ffffff;
    padding: 3px 20px;
}

.temp {
    grid-column: 1/2;
    font-weight: bold;
    font-size: 56px;
}

.condition{
    font-weight: bold;
    font-size: 20px;
}

.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 30px;
}

#tempToggle {
    -webkit-appearance: none;
    appearance: none;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--colorBeforeCheck);
    border-radius: 28px;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    box-shadow: var(--shadow);
}

.slider .unit {
    position: absolute;
    left: 8px;
    bottom:6px;
    font-size: 14px;
    font-weight: bold;
    color: white;
    transition: transform 0.4s;
}

input:checked + .slider {
    background-color: var(--colorOnCheck);
}

input:checked + .slider .unit {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}

@media screen and (max-width: 850px) {
    form{
        width: 50%;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,UAAU;IACV,SAAS;IACT,mJAAmJ;AACvJ;AACA;IACI,6BAA6B;IAC7B,uCAAuC;IACvC,gCAAgC;IAChC,mCAAmC;IACnC,sCAAsC;AAC1C;;AAEA;IACI,aAAa;IACb,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;IACT,kDAAkC;IAClC,sBAAsB;IACtB,4BAA4B;AAChC;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,SAAS;IACT,qCAAqC;AACzC;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,oDAAkD;IAClD,uBAAuB;IACvB,4BAA4B;AAChC;;AAEA;IACI,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,WAAW;IACX,kBAAkB;IAClB,YAAY;IACZ,mBAAmB;IACnB,aAAa;IACb,yBAAyB;IACzB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,+BAA+B;IAC/B,4BAA4B;IAC5B,yBAAyB;IACzB,mBAAmB;IACnB,sBAAsB;IACtB,gBAAgB;AACpB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;IACjB,cAAc;IACd,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,sBAAsB;IACtB,4BAA4B;IAC5B,6BAA6B;IAC7B,cAAc;IACd,iBAAiB;AACrB;;AAEA;IACI,gBAAgB;IAChB,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,qBAAqB;IACrB,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,wBAAwB;IACxB,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,MAAM;IACN,OAAO;IACP,QAAQ;IACR,SAAS;IACT,yCAAyC;IACzC,mBAAmB;IACnB,wBAAwB;IACxB,gBAAgB;IAChB,yBAAyB;AAC7B;;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,UAAU;IACV,eAAe;IACf,iBAAiB;IACjB,YAAY;IACZ,0BAA0B;AAC9B;;AAEA;IACI,qCAAqC;AACzC;;AAEA;IACI,mCAAmC;IACnC,+BAA+B;IAC/B,2BAA2B;AAC/B;;AAEA;IACI;QACI,UAAU;IACd;AACJ","sourcesContent":["* {\n    padding: 0;\n    margin: 0;\n    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;\n}\n:root {\n    --bgColor: rgb(237, 233, 247);\n    --shadow: 3px 3px 3px rgb(0, 0, 0, 0.3);\n    --headerColour : rgb(13, 20, 42);\n    --colorOnCheck : rgb(141, 179, 249);\n    --colorBeforeCheck : rgb(96, 106, 170);\n}\n\nbody {\n    height: 100vh;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 1rem;\n    background:url(\"../images/bg.jpg\");\n    backgr